<template>
  <div>
    <v-card-title>
      <div class="title">
        {{ $vuetify.lang.t(messages.title) }}
      </div>
    </v-card-title>
    <v-form class="ml-16">
      <v-card class="ml-6" color="#f1f5fc" max-width="900px">
        <v-card-text>
          <span class="text-h7 text-center body-2 black--text text--darken-1">
            {{ $vuetify.lang.t("$vuetify.doctor.title_basic") }}
          </span>
          <v-container>
            <v-row wrap>
              <v-col cols="12" sm="6" class="mb-0 py-0">
                <v-text-field
                  v-model="form.name"
                  color="#007bff"
                  :label="$vuetify.lang.t('$vuetify.hospitals.cols.name.title')"
                  @blur="$v.form.name.$touch()"
                  @input="$v.form.name.$touch()"
                  :error-messages="nameErrors"
                  class="font-weight-bold"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" class="mb-0 py-0">
                <v-file-input
                  v-model="form.logo"
                  :rules="rules"
                  accept="image/png, image/jpeg, image/bmp"
                  prepend-icon="mdi-camera"
                  :label="$vuetify.lang.t('$vuetify.hospitals.cols.logo.title')"
                  @change="selectFile"
                ></v-file-input>
                <!--                <v-text-field-->
                <!--                    v-model="form.logo"-->
                <!--                    color="#007bff"-->
                <!--                    :label="$vuetify.lang.t('$vuetify.hospitals.cols.logo.title')"-->
                <!--                    class="font-weight-bold"-->
                <!--                ></v-text-field>--> </v-col
              ><v-col cols="12" sm="6" class="mb-0 py-0">
                <v-text-field
                  v-model="form.slogan"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.hospitals.cols.slogan.title')
                  "
                  class="font-weight-bold"
                ></v-text-field> </v-col
              ><v-col cols="12" sm="6" class="mb-0 py-0">
                <v-text-field
                  v-model="form.taxpayer"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.hospitals.cols.taxpayer.title')
                  "
                  class="font-weight-bold"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-form>
    <v-form class="mt-5 ml-16">
      <v-card class="ml-6" max-width="900px" color="#f1f5fc">
        <v-card-text>
          <span class="text-h7 text-center body-2 black--text text--darken-1">
            {{ $vuetify.lang.t("$vuetify.hospitals.contact") }}
          </span>
          <v-container>
            <v-row wrap>
              <v-col cols="12" sm="6" class="mb-0 py-0">
                <v-text-field
                  v-model="form.phone"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.hospitals.cols.phone.title')
                  "
                  @blur="$v.form.phone.$touch()"
                  @input="$v.form.phone.$touch()"
                  :error-messages="phoneErrors"
                  class="font-weight-bold"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" class="mb-0 py-0">
                <v-text-field
                  v-model="form.address"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.hospitals.cols.address.title')
                  "
                  class="font-weight-bold"
                  :error-messages="addressErrors"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" class="mb-0 py-0">
                <v-text-field
                  v-model="form.email"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.hospitals.cols.email.title')
                  "
                  @blur="$v.form.email.$touch()"
                  @input="$v.form.email.$touch()"
                  :error-messages="emailErrors"
                  class="font-weight-bold"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" class="mb-0 py-0">
                <v-text-field
                  v-model="form.zip_code"
                  color="#007bff"
                  :label="
                    $vuetify.lang.t('$vuetify.hospitals.cols.zip_code.title')
                  "
                  class="font-weight-bold"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
      </v-card>
    </v-form>
    <v-form class="mt-5 ml-16">
      <v-card class="ml-6" max-width="900px" color="#f1f5fc">
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="#007bff"
            dark

            :disabled="items === []"
          >
            {{ $vuetify.lang.t("$vuetify.btn.save") }} </v-btn
          ><v-btn color="#007bff" dark @click="saveHospitals">
            {{ $vuetify.lang.t("$vuetify.btn.update") }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-form>
  </div>
</template>

<script>
// import FormDepartment from "./FormDepartment";
import FormMixin from "./../../mixins/Common/Form.vue";
// import {mapGetters} from "vuex";
import { required, maxLength, email } from "vuelidate/lib/validators";
import { phoneR, existNameHospital } from "../../helpers/Validator";

export default {
  mixins: [FormMixin],
  props: {
    form: {
      type: Object,
      default: function() {
        return {
          id: null,
          name: null,
          taxpayer: null,
          slogan: null,
          address: null,
          zip_code: null,
          logo: null,
          phone: null,
          email: null
          // depart: null
        };
      }
    }
  },
  data: () => ({
    formData: {
      id: null,
      name: null
    },
    dialogForm: {
      show: false,
      shows: {
        showFilter: false,
        showInfo: false
      }
    },
    rules: [
      value =>
        !value ||
        value.size < 2000000 ||
        "Avatar size should be less than 2 MB!"
    ],
    loading: {
      list: false,
      refresh: false,
      filter: false
    },
    items: [],
    urlItems: "hospitals",
    urlUsers: "users",
    isLoading: false,
    search: null,
    itemsUsers: []
  }),
  mounted() {
    this.getItems();
  },
  watch: {},
  created() {},
  validations() {
    let validators = {
      form: {
        name: {
          required,
          unique: existNameHospital,
          maxLength: maxLength(255)
        },
        email: {
          email,
          maxLength: maxLength(255)
        },
        phone: {
          required,
          phoneR
        },
        address: {
          required
        }
      }
    };
    return validators;
  },
  methods: {
    getItems() {
      let self = this;
      return new Promise((resolve, reject) => {
        this.loading.list = true;
        self.$store
          .dispatch("request", {
            url: self.urlItems
          })
          .then(response => {
            let data = response.data;
            console.log(data.content);
            if (data.content !== []) {
              self.items = data.content;
              this.form.id = this.items[0].id;
              this.form.name = this.items[0].name;
              this.form["oldName"] = this.items[0].name;
              this.form.address = this.items[0].address;
              this.form.taxpayer = this.items[0].taxpayer;
              this.form.slogan = this.items[0].slogan;
              this.form.phone = this.items[0].phone;
              this.form.logo = this.items[0].logo;
              this.form.email = this.items[0].email;
            }
            self.meta = { totalElements: data.totalElements };
            // console.log("meta", data.content[0]);
            resolve(response);
          })
          .catch(err => {
            let message = this.$vuetify.lang.t("$vuetify.error_occured");
            if (err.response) {
              if (err.response.status === 400) {
                self.setApiFormErrors(err.response.data.data);
                message = self.$vuetify.lang.t("$vuetify.error_form");
              } else if (err.response.status === 403) {
                message = self.$vuetify.lang.t("$vuetify.error_denied");
              } else if (err.response.status === 500) {
                message = self.$vuetify.lang.t("$vuetify.error_server");
              }
            }
            self.$store.dispatch("showNotification", {
              status: true,
              text: message
            });
            reject(err);
          })
          .then(() => {
            self.loading.list = false;
            resolve();
          });
      });
    },
    selectFile(file) {
      this.logo = file;
      console.log(this.logo);
    },
    createD() {
      this.dialogForm.show = true;
      this.formData = {
        id: null,
        name: null
      };
    },
    // Fonction pour récuperer les types de roles dans la store

    // Fonction pour récuperer le contenu des champs du formulaire
    getDataForm() {
      let data = {
        taxpayer: this.form.taxpayer,
        slogan: this.form.slogan,
        address: this.form.address,
        logo: this.form.logo,
        zip_code: this.form.zip_code,
        name: this.form.name,
        phone: this.form.phone,
        email: this.form.email
        // depart: this.form.depart
      };
      return data;
    },
    setForm(item) {
      // Données envoyées lors de la modification
      this.form.id = item.id;
      this.form.name = item.name;
      this.form["oldName"] = item.name;
      this.form.address = item.address;
      this.form.taxpayer = item.taxpayer;
      this.form.slogan = item.slogan;
      this.form.phone = item.phone;
      this.form.logo = item.logo;
      this.form.email = item.email;
      // this.form.depart = item.depart["id"];
      // this.itemsDepartments.push(item.depart);
    }
  },
  computed: {
    // Gestion des erreurs du formulaire
    nameErrors() {
      let errors = [];
      if (!this.$v.form.name.$dirty) return errors;
      !this.$v.form.name.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.hospitals.cols.name.required")
        );
      !this.$v.form.name.unique &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.hospitals.cols.name.unique")
        );

      return errors;
    },
    addressErrors() {
      let errors = [];
      if (!this.$v.form.address.$dirty) return errors;
      !this.$v.form.address.required &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.hospitals.cols.address.required")
        );

      return errors;
    },
    emailErrors() {
      let errors = [];
      if (!this.$v.form.email.$dirty) return errors;

      !this.$v.form.email.email &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.hospitals.cols.email.invalid")
        );
      return errors;
    },
    phoneErrors() {
      let errors = [];
      if (!this.$v.form.phone.$dirty) return errors;
      !this.$v.form.phone.phoneR &&
        errors.push(
          this.$vuetify.lang.t("$vuetify.hospitals.cols.phone.invalid")
        );
      return errors;
    }
  },
  components: {
    // FormDepartment
  }
};
</script>
